
.App {
  text-align: center;
  min-height: 100vh;
  max-width: 100%;
  font-family: MyFont !important; 

}
.Manifesto{
  font-size: 1.5em;
  padding-right: 10vw;
  padding-left: 10vw;
  padding-top: 100px;
  padding-bottom:100px;
  transform: translateY(80vh);
  background: #008590;
  color:white;
  margin: 30px;
  transition: opacity 1s;
}
.box{
  padding: 30px;
  margin: 20px;
}
#Busca{
  background-color: #f9b427;
    color: white;
    font-size: 1.2em;
    margin-left: -20px;
    width: 85vw;
    padding-left: 50px;
}
.TextLeft{
  text-align: left;
}
#ManifestoText{
  opacity:0;
  transition: opacity 1s;
}
.ManifestoVisible{
  opacity: 1 !important;
}
.Bienvenidos{
  font-size: 2.5em;
  position: absolute;
  width: 100vw;
  z-index: 1;
  text-align: center;
  top:calc(150vh - 1.25em);
}
@font-face {
  font-family: MyFont;
  src: url('./font/Champagne\ &\ Limousines.ttf');
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.AppHeader{
  background-color: black;
  height: 300px;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.circle{
  height: 1.25em;
    width: 1.25em;
    border-width: 0.2em;
    margin: 0.5em;
    border-style: solid;
    border-color: rgb(153,153,153);
    border-radius: 0.625em;
  transition: all 0.5s;

}
.circleActive{
  background-color: rgb(153,153,153) !important;
}
.circleWrapper{
  position:fixed;
  top: calc(50% - 7em);
  transition: opacity 0.5s;
  opacity: 0;
  right: 15px;
}
.circleVisible{
  opacity: 1 !important;
}
.paperUP{
  animation-name: App-logo-spin;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  transition: all 1s;
}
.paperDOWN{
  animation-name: App-logo-spin2;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  transition: all 1s;
}
.parallax_group{
  position: relative;
  height: 100vh;
  transition: transform 0.5s;
  transform-style: preserve-3d;
}
#Hlayer2 {
  transform: translateZ(0);
}
#Hlayer1 {
  transform: translateZ(-1px) scale(2);
}
.App-link {
  color: #61dafb;
}
#Paper1{
  top:30px;
  right:60px ;
  bottom:auto;
  left:auto;
}
#Paper2{
  top:auto;
  left:auto;
  bottom:60px;
  right: 90px;
}
#Paper3{
  right: auto;
  bottom:auto;
  top:30px;
  left: 40px;
}
#Paper4{
  top: auto;
  right: auto;
  bottom:100px;
  left: 20px;
}
#Paper5{
  bottom:30px;
  left: auto;
  top:auto;
  right: 60px;
}
#Paper6{
  top:100px;
  right:90px ;
  bottom:auto;
  left:auto;
}
#Paper7{
  top: auto;
  right: auto;
  bottom:150px;
  left: 17  0px;
}
#Paper8{
  top:170px;
  left:120px ;
  bottom:auto;
