  bottom:auto;
  right:auto;
}

.tall{
  height:25vh;
  position: absolute;
  width:15vh;
}
.med{
  height:20vh;
  width:12vh;
  position: absolute;
}
.small{
  height:15vh;
  position: absolute;
  width:9vh;
}
@keyframes App-logo-spin {
  from {
    margin-top: 0px;
  }
  to {
    margin-top: 20px;
  }
}
@keyframes App-logo-spin2 {
  from {
    margin-bottom: 20px;
  }
  to {
    margin-bottom: 0  px;
  }
}

.Rombo{
  background-image: url('./assets/logo315.png');
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  
}